import React, { Fragment, useState, useCallback } from "react"
import { hot } from "react-hot-loader"
import { State } from "router5"
import { useRoute, Link } from "react-router5"
import clsx from "clsx"
import {
  CssBaseline,
  Drawer,
  AppBar,
  Toolbar,
  List,
  Typography,
  Divider,
  IconButton,
  Badge,
  Container,
  ListItem,
  ListItemIcon,
  ListItemText,
  ListSubheader,
  makeStyles,
} from "@material-ui/core"
import MenuRoundedIcon from "@material-ui/icons/MenuRounded"
import MenuOpenRoundedIcon from "@material-ui/icons/MenuOpenRounded"
import DescriptionRoundedIcon from "@material-ui/icons/DescriptionRounded"
import NotificationsActiveRoundedIcon from "@material-ui/icons/NotificationsActiveRounded"
import AccountCircleRoundedIcon from "@material-ui/icons/AccountCircleRounded"
import GroupRoundedIcon from "@material-ui/icons/GroupRounded"
import ArchiveRoundedIcon from "@material-ui/icons/ArchiveRounded"
import PermMediaRoundedIcon from "@material-ui/icons/PermMediaRounded"
import CreateRoundedIcon from "@material-ui/icons/CreateRounded"
import LabelRoundedIcon from "@material-ui/icons/LabelRounded"
import DraftsRoundedIcon from "@material-ui/icons/DraftsRounded"
import InsertDriveFileRoundedIcon from "@material-ui/icons/InsertDriveFileRounded"
import {
  AuthorsPage,
  MediaPage,
  TagsPage,
  PublicationsArchivePage,
  PublicationsCreatePage,
  PublicationsDraftPage,
  PublicationsPublishedPage,
  PublicationsReadyPage,
  ManagementUsersPage,
  UserPage,
} from "@pages"

const drawerWidth = 260

const useStyles = makeStyles((theme) => ({
  root: {
    display: "flex",
  },
  toolbar: {
    paddingRight: 24, // keep right padding when drawer closed
  },
  toolbarIcon: {
    display: "flex",
    alignItems: "center",
    justifyContent: "flex-end",
    padding: "0 8px",
    ...theme.mixins.toolbar,
  },
  appBar: {
    zIndex: theme.zIndex.drawer + 1,
    transition: theme.transitions.create(["width", "margin"], {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
  },
  appBarShift: {
    marginLeft: drawerWidth,
    width: `calc(100% - ${drawerWidth}px)`,
    transition: theme.transitions.create(["width", "margin"], {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.enteringScreen,
    }),
  },
  menuButton: {
    marginRight: 36,
  },
  menuButtonHidden: {
    display: "none",
  },
  title: {
    flexGrow: 1,
  },
  drawerPaper: {
    position: "relative",
    whiteSpace: "nowrap",
    width: drawerWidth,
    transition: theme.transitions.create("width", {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.enteringScreen,
    }),
  },
  drawerPaperClose: {
    overflowX: "hidden",
    transition: theme.transitions.create("width", {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
    width: theme.spacing(7),
    [theme.breakpoints.up("sm")]: {
      width: theme.spacing(9),
    },
  },
  content: {
    paddingTop: "60px",
    flexGrow: 1,
    height: "100vh",
    overflow: "auto",
  },
  container: {
    paddingTop: theme.spacing(4),
    paddingBottom: theme.spacing(4),
  },
  paper: {
    padding: theme.spacing(2),
    display: "flex",
    overflow: "auto",
    flexDirection: "column",
  },
  fixedHeight: {
    height: 240,
  },
  chips: {
    display: "flex",
    flexWrap: "wrap",
  },
  chip: {
    margin: 2,
  },
}))

export const App = hot(module)(function App() {
  const classes = useStyles()
  const { route } = useRoute()

  const [opened, setOpened] = useState(true)

  const handleDrawerOpen = useCallback(() => {
    setOpened(true)
  }, [setOpened])

  const handleDrawerClose = useCallback(() => {
    setOpened(false)
  }, [setOpened])

  const setPageByRoute = (route: State) => {
    if (typeof route === "undefined" || route === null || !("name" in route)) {
      return null
    }

    switch (route.name) {
      case "admin.account":
        return <UserPage />
      case "admin":
      case "admin.publications":
        return <PublicationsCreatePage />
      case "admin.publications.ready":
        return <PublicationsReadyPage />
      case "admin.publications.draft":
        return <PublicationsDraftPage />
      case "admin.publications.published":
        return <PublicationsPublishedPage />
      case "admin.publications.archive":
        return <PublicationsArchivePage />
      case "admin.dictionaries.authors":
        return <AuthorsPage />
      case "admin.dictionaries.tags":
        return <TagsPage />
      case "admin.dictionaries.media":
        return <MediaPage />
      case "admin.management.users":
        return <ManagementUsersPage />
      default:
        return null
    }
  }

  return (
    <Fragment>
      <CssBaseline />
      <div className={classes.root}>
        <AppBar position="absolute" className={clsx(classes.appBar, opened && classes.appBarShift)}>
          <Toolbar className={classes.toolbar}>
            <IconButton
              edge="start"
              color="inherit"
              aria-label="open drawer"
              onClick={handleDrawerOpen}
              className={clsx(classes.menuButton, opened && classes.menuButtonHidden)}
            >
              <MenuRoundedIcon />
            </IconButton>
            <Typography
              component="h1"
              variant="h6"
              color="inherit"
              noWrap
              className={classes.title}
            >
              Dashboard
            </Typography>
            <IconButton color="inherit">
              <Badge badgeContent={4} color="secondary">
                <NotificationsActiveRoundedIcon />
              </Badge>
            </IconButton>
            <Link routeName="admin.account">
              <IconButton color="inherit">
                <AccountCircleRoundedIcon htmlColor="white" />
              </IconButton>
            </Link>
          </Toolbar>
        </AppBar>
        <Drawer
          variant="permanent"
          classes={{
            paper: clsx(classes.drawerPaper, !opened && classes.drawerPaperClose),
          }}
          open={opened}
        >
          <div className={classes.toolbarIcon}>
            <IconButton onClick={handleDrawerClose}>
              <MenuOpenRoundedIcon />
            </IconButton>
          </div>
          <Divider />
          <List>
            <ListSubheader inset={true}></ListSubheader>
            <Link routeName="admin.publications">
              <ListItem button={true}>
                <ListItemIcon style={{ paddingLeft: "7px" }}>
                  <DescriptionRoundedIcon />
                </ListItemIcon>
                <ListItemText primary="" />
              </ListItem>
            </Link>
            <Link routeName="admin.publications.draft">
              <ListItem button={true}>
                <ListItemIcon style={{ paddingLeft: "7px" }}>
                  <DraftsRoundedIcon />
                </ListItemIcon>
                <ListItemText primary="" />
              </ListItem>
            </Link>
            <Link routeName="admin.publications.ready">
              <ListItem button={true}>
                <ListItemIcon style={{ paddingLeft: "7px" }}>
                  <InsertDriveFileRoundedIcon />
                </ListItemIcon>
                <ListItemText primary="  " />
              </ListItem>
            </Link>
            <Link routeName="admin.publications.published">
              <ListItem button={true}>
                <ListItemIcon style={{ paddingLeft: "7px" }}>
                  <DescriptionRoundedIcon />
                </ListItemIcon>
                <ListItemText primary="" />
              </ListItem>
            </Link>
            <Link routeName="admin.publications.archive">
              <ListItem button={true}>
                <ListItemIcon style={{ paddingLeft: "7px" }}>
                  <ArchiveRoundedIcon />
                </ListItemIcon>
                <ListItemText primary="" />
              </ListItem>
            </Link>
          </List>
          <Divider />
          <List>
            <ListSubheader inset={true}></ListSubheader>
            <Link routeName="admin.management.users">
              <ListItem button={true}>
                <ListItemIcon style={{ paddingLeft: "7px" }}>
                  <GroupRoundedIcon />
                </ListItemIcon>
                <ListItemText primary="" />
              </ListItem>
            </Link>
          </List>
          <Divider />
          <List>
            <ListSubheader inset={true}></ListSubheader>
            <Link routeName="admin.dictionaries.authors">
              <ListItem button={true}>
                <ListItemIcon style={{ paddingLeft: "7px" }}>
                  <CreateRoundedIcon />
                </ListItemIcon>
                <ListItemText primary="" />
              </ListItem>
            </Link>
            <Link routeName="admin.dictionaries.tags">
              <ListItem button={true}>
                <ListItemIcon style={{ paddingLeft: "7px" }}>
                  <LabelRoundedIcon />
                </ListItemIcon>
                <ListItemText primary="" />
              </ListItem>
            </Link>
            <Link routeName="admin.dictionaries.media">
              <ListItem button={true}>
                <ListItemIcon style={{ paddingLeft: "7px" }}>
                  <PermMediaRoundedIcon />
                </ListItemIcon>
                <ListItemText primary="" />
              </ListItem>
            </Link>
          </List>
        </Drawer>
        <main className={classes.content}>
          <Container maxWidth="lg" className={classes.container}>
            {setPageByRoute(route)}
          </Container>
        </main>
      </div>
    </Fragment>
  )
})
