import React, { useState, useCallback, useRef, RefObject, FormEvent } from "react"
import {
  Typography,
  Grid,
  TextField,
  InputLabel,
  Input,
  Select,
  MenuItem,
  Chip,
  Button,
} from "@material-ui/core"
import FroalaEditor from "react-froala-wysiwyg"
import "froala-editor/css/froala_style.min.css"
import "froala-editor/css/froala_editor.pkgd.min.css"
import { articleApi, Article } from "@api/article"

const tags = ["", "", "", ""]

const ITEM_HEIGHT = 48
const ITEM_PADDING_TOP = 8
const MenuProps = {
  PaperProps: {
    style: {
      maxHeight: ITEM_HEIGHT * 4.5 + ITEM_PADDING_TOP,
      width: 150,
    },
  },
}

export const PublicationsCreatePage = () => {
  const fileInputRef: RefObject<HTMLInputElement> | null = useRef(null)
  const [article, setArticle] = useState<Article>({
    userId: 1,
    title: "",
    lead: "",
    body: "",
    type: "",
    keywords: "",
    description: "",
    cover: "",
    tags: [],
  })

  const handleChangeFormField = useCallback(
    (field: string) => (event) => {
      if (field === "cover") {
        if (fileInputRef.current) {
          const fileInput: HTMLInputElement = fileInputRef.current
          if (fileInput.files !== null && fileInput.files.length === 1) {
            const data = Object.assign({}, article, { [field]: fileInput.files[0] })
            setArticle(data)
            return
          }
        }
      }
      if (field === "body") {
        const data = Object.assign({}, article, { [field]: event })
        setArticle(data)
        return
      }
      const data = Object.assign({}, article, { [field]: event.target.value })
      setArticle(data)
    },
    [article, setArticle],
  )

  const submit = useCallback(
    (event: FormEvent) => {
      event.preventDefault()
      articleApi.create(article)
    },
    [articleApi],
  )

  return (
    <form onSubmit={submit}>
      <Typography variant="h3" component="h1" gutterBottom={true}>
         
      </Typography>
      <Grid container={true} spacing={7}>
        <Grid item={true} xs={12}>
          <InputLabel id="type"></InputLabel>
          <Select
            fullWidth
            labelId="type"
            id="type"
            value={article.type}
            onChange={handleChangeFormField("type")}
          >
            <MenuItem value="newsfeed"></MenuItem>
            <MenuItem value="people"></MenuItem>
            <MenuItem value="detailed"> </MenuItem>
            <MenuItem value="in-russia"> </MenuItem>
            <MenuItem value="calendar"></MenuItem>
          </Select>
        </Grid>
        <Grid item={true} xs={12}>
          <TextField
            id="title"
            fullWidth
            label=""
            value={article.title}
            onChange={handleChangeFormField("title")}
          />
        </Grid>
        <Grid item={true} xs={12}>
          <TextField
            id="lead"
            fullWidth
            label=""
            value={article.lead}
            onChange={handleChangeFormField("lead")}
          />
        </Grid>
        <Grid item={true} xs={12}>
          <input
            accept="image/*"
            id="cover"
            ref={fileInputRef}
            multiple
            type="file"
            style={{ display: "none" }}
            onChange={handleChangeFormField("cover")}
          />
          <label htmlFor="cover">
            <Button variant="contained" color="primary" component="span">
              Upload
            </Button>
          </label>
        </Grid>
        <Grid item={true} xs={12}>
          <InputLabel id="tags"></InputLabel>
          <Select
            labelId="tags"
            multiple={true}
            fullWidth
            value={article.tags}
            onChange={handleChangeFormField("tags")}
            input={<Input id="tags" />}
            renderValue={(selected) => (
              <div className={classes.chips}>
                {(selected as string[]).map((value) => (
                  <Chip key={value} label={value} className={classes.chip} />
                ))}
              </div>
            )}
            MenuProps={MenuProps}
          >
            {tags.map((tag) => (
              <MenuItem key={tag} value={tag}>
                {tag}
              </MenuItem>
            ))}
          </Select>
        </Grid>
        <Grid item={true} xs={12}>
          <FroalaEditor
            tag="textarea"
            model={article.body}
            onModelChange={handleChangeFormField("body")}
          />
        </Grid>
        <Grid item={true} xs={12}>
          <TextField
            id="keywords"
            fullWidth
            label="Keywords"
            value={article.keywords}
            onChange={handleChangeFormField("keywords")}
          />
        </Grid>
        <Grid item={true} xs={12}>
          <TextField
            id="description"
            fullWidth
            label="Description"
            value={article.description}
            onChange={handleChangeFormField("description")}
          />
        </Grid>
        <Grid item={true} xs={12}>
          <Button variant="contained" color="primary" type="submit">
            
          </Button>
        </Grid>
      </Grid>
    </form>
  )
}
